import React from "react";
import {
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  SelectChangeEvent,
} from "@mui/material";
import PeriodSelectStyles from "./PeriodSelect.module.scss";

const PeriodSelect = () => {
  const [timePeriod, setTimePeriod] = React.useState<string>("");
  const [totalPeriod, setTotalPeriod] = React.useState<string>(" ");

  const handleChange = (event: SelectChangeEvent) => {
    setTimePeriod(event.target.value);
  };

  const handleTotalChange = (event: SelectChangeEvent) => {
    setTotalPeriod(event.target.value);
  };

  return (
    <div className={PeriodSelectStyles.formWrapper}>
      <FormControl fullWidth className={PeriodSelectStyles.formControl}>
        <InputLabel id="total-period-select-label"> </InputLabel>
        <Select
          labelId="total-period-select-label"
          id="total-period-select"
          value={totalPeriod}
          label="Total Period"
          onChange={handleTotalChange}
          className={PeriodSelectStyles.select}
          renderValue={(value) =>
            value === "month" ? " " : "  "
          }
        >
          <MenuItem value={"total"}>  </MenuItem>
          <MenuItem value={"month"}> </MenuItem>
        </Select>
      </FormControl>

      <FormControl fullWidth className={PeriodSelectStyles.formControl}>
        <InputLabel id="time-period-select-label"> </InputLabel>
        <Select
          labelId="time-period-select-label"
          id="time-period-select"
          value={timePeriod}
          label="Select Month"
          onChange={handleChange}
          className={PeriodSelectStyles.select}
          renderValue={(value) =>
            `${value.charAt(0).toUpperCase() + value.slice(1)}`
          }
        >
          <MenuItem value={""}></MenuItem>
          <MenuItem value={""}></MenuItem>
          <MenuItem value={""}></MenuItem>
          <MenuItem value={""}></MenuItem>
          <MenuItem value={""}></MenuItem>
          <MenuItem value={""}></MenuItem>
          <MenuItem value={""}></MenuItem>
          <MenuItem value={""}></MenuItem>
          <MenuItem value={""}></MenuItem>
          <MenuItem value={""}></MenuItem>
          <MenuItem value={""}></MenuItem>
          <MenuItem value={""}></MenuItem>
        </Select>
      </FormControl>
    </div>
  );
};

export default PeriodSelect;
